import React, { useEffect, useState } from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from '../../../componenets/Page';
import Results from './Results';
import Toolbar from './Toolbar';
import { getOrganizationEmployees } from '../../../services/api';
import data from './data';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  }
}));


const ScheduleListView = () => {
  const classes = useStyles();
  const [employees, setEmployee] = useState([]);
  let employeeArray =[];
  
  function createData(id, username, firstName, lastName) {
    return { id, username, firstName, lastName };
  }
  
  useEffect(() => {
    getOrganizationEmployees().then((res) => {
      const data = res.data;
      console.log("Data: "+data);
      for(let employee of data) {
        employeeArray.push(createData(employee._id, employee.username, employee.firstName, employee.lastName))
      }
      setEmployee(employeeArray);
      console.log(employees);
    })
    
  }, [])

  const today = new Date();
  const [date, setDate] = useState(today.setDate(today.getDate() + (7-today.getDay())%7+1));

  return (
    <Page
      className={classes.root}
      title="Schedule"
    >
      <Container maxWidth={false}>
        <Toolbar date={date} onNextClick={(value) => setDate(value)} onPrevClick={(value) => setDate(value)} />
        <Box mt={3}>
          <Results date={date} employees={employees} />
        </Box>
      </Container>
    </Page>
  );
};

export default ScheduleListView;
