import React, { useState } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  makeStyles,
  Button
} from '@material-ui/core';
import getInitials from '../../../utility/getInitials';
import ScheduleDilougForm from './ScheduleDilougForm';
import {addSchedule, getSchedule} from '../../../services/api';

const useStyles = makeStyles((theme) => ({
  root: {},
  avatar: {
    marginRight: theme.spacing(2)
  }
}));


const Results = ({ className, date, employees, ...rest }) => {
  const classes = useStyles();

  // console.log(employees);
  const monday = date;
  const tuesday = date + 1 * 24 * 60 * 60 * 1000;
  const wednesday = date + 2 * 24 * 60 * 60 * 1000;
  const thursday = date + 3 * 24 * 60 * 60 * 1000;
  const friday = date + 4 * 24 * 60 * 60 * 1000;
  const satday = date + 5 * 24 * 60 * 60 * 1000;
  const sunday = date + 6 * 24 * 60 * 60 * 1000;

  function displayDate(date){
    return new Date(date).toString().slice(0,10);
  }

  //
  const getTimeEntries = (entries) => {
    // console.log(entries);
    addSchedule(entries).then((response) => {
      if(response.status === 'ok') {
        // console.log(response);
      } else {
        // Failed
      };
  })

  getSchedule('60668468ed2e7d04a078d459').then((response) => {
    if(response.status === 'ok') {
      console.log(response);
    } else {
      // Failed
    };
  })
}


  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <PerfectScrollbar>
        <Box minWidth={1050}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  Name
                </TableCell>
                <TableCell>
                  {displayDate(monday)}
                </TableCell>
                <TableCell>
                  {displayDate(tuesday)}
                </TableCell>
                <TableCell>
                  {displayDate(wednesday)}
                </TableCell>
                <TableCell>
                  {displayDate(thursday)}
                </TableCell>
                <TableCell>
                  {displayDate(friday)}
                </TableCell>
                <TableCell>
                  {displayDate(satday)}
                </TableCell>
                <TableCell>
                  {displayDate(sunday)}
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {employees.map((employee) => (
                <TableRow
                  hover
                  key={employee.id}
                >
                  <TableCell>
                    <Box
                      alignItems="center"
                      display="flex"
                    >
                      <Typography
                        color="textPrimary"
                        variant="body1"
                      >
                        {employee.firstName +" "+ employee.lastName}
                      </Typography>
                    </Box>
                  </TableCell>
                  <TableCell>
                    <ScheduleDilougForm id={employee.id} startdate={monday} date={monday} day={'monday'} onAdd={getTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} startdate={monday} date={tuesday} day={'tuesday'} onAdd={getTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} startdate={monday} date={wednesday} day={'wednesday'} onAdd={getTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} startdate={monday} date={thursday} day={'thursday'} onAdd={getTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} startdate={monday} date={friday} day={'friday'} onAdd={getTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} startdate={monday} date={satday} day={'saturday'} onAdd={getTimeEntries}/>
                  </TableCell>
                  <TableCell>
                  <ScheduleDilougForm id={employee.id} startdate={monday} date={sunday} day={'sunday'} onAdd={getTimeEntries}/>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  employees: PropTypes.array.isRequired
};

export default Results;
